import React from 'react';
import { BackTop } from 'antd';
// import './index.css';

export default class RightBar extends React.Component{
    state = {
        QRVisible:false,
    }
    toggleQRVisible = (e)=>{
        e && e.stopPropagation();
        this.setState({
            QRVisible:!this.state.QRVisible,
        })
    }
    render(){
        const {QRVisible} = this.state;
        return <React.Fragment>
                <div className="fixed fixed-qr">
                    <div className="qr-code-list" style={QRVisible?{display:'block'}:{}}>
                    <div className="qr-code">
                        <img src={`${process.env.REACT_APP_IMAGE_PUBLIC_URL}/right_bar_qr1.png`} alt=''/>
                        <div className="title">商家版APP</div>
                    </div>
                    <div className="qr-code">
                        <img src={`${process.env.REACT_APP_IMAGE_PUBLIC_URL}/right_bar_qr2.png`} alt=''/>
                        <div className="title">消费版APP</div>
                    </div>
                
                    <div className="qr-code">
                        <img src={`${process.env.REACT_APP_IMAGE_PUBLIC_URL}/right_bar_qr3.png`} alt=''/>
                        <div className="title">加入QQ群了解更多</div>
                    </div>
                    </div>
                    {
                        QRVisible && <div className='line'></div>
                    }
                    <div className="more-qr" onClick={this.toggleQRVisible}></div>
                    <BackTop className='right-bar'>
                        <div className='line'></div>
                        <div className="goto-top" id='back-top'></div>
                    </BackTop>
                </div>
            </React.Fragment>
    }
}