import '../../common';// must first line becase of the pollyfill
import smoothscroll from 'smoothscroll-polyfill';
import React from 'react';
import ReactDOM from 'react-dom';
import './index.less';
import RightBar from 'component/RightBar';
import 'component/RightBar/index.css';
import Swiper from 'swiper';

import * as serviceWorker from '../../serviceWorker';

ReactDOM.render(<RightBar/>, document.getElementById('right-bar-container'));

//scroll-behavior: smooth;
smoothscroll.polyfill();
let nodeList = document.querySelectorAll('.top-bar a[href]');
for(let i=0;i<nodeList.length;i++){
    let node = nodeList[i];
    node.addEventListener('click',(e)=>{
        e.preventDefault();
        document.getElementById(e.target.hash.replace('#','')).scrollIntoView({ behavior: 'smooth' });
    },true);
}

// 页面上图片轮播
/*var swiper = */ new Swiper('.first-page-container .swiper-container',{
    loop: true,
    autoplay: {
        delay: 3000,
        disableOnInteraction: false,
    },
});

/*var collaborator_swiper = */new Swiper('.collaborator .swiper-container',{
    loop: true,
    autoplay: {
        delay: 3000,
        disableOnInteraction: false,
    },
    navigation: {
        nextEl: '.swiper-button-next',
        prevEl: '.swiper-button-prev',
    },
});

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
